.app:before {
  content: "";
  z-index: -1;
  opacity: .5;
  background-color: #357dba;
  background-position: center;
  background-repeat: repeat;
  background-size: 250%;
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
}

.app:after {
  content: "";
  z-index: -1;
  opacity: .25;
  filter: blur(2em);
  background-color: #cfba98;
  background-repeat: repeat;
  background-size: 500px;
  width: 100%;
  height: 100%;
  position: absolute;
  top: -50%;
  left: -50%;
}

.scroll-container, .scroll-area {
  max-width: 850px;
