.scroll-container {
  scroll-snap-type: y mandatory;
  overflow: auto;
}

.scroll-area {
  scroll-snap-align: start;
}

.scroll-container, .scroll-area {
  margin: 0 auto;
}

.scroll-area {
  color: var(--white);
  justify-content: center;
  align-items: center;
  display: flex;
}

.scroll-area:first-of-type {
  background: #49b293;
}

.scroll-area:nth-of-type(2) {
  background: #c94e4b;
}

.scroll-area:nth-of-type(3) {
  background: #4cc1be;
}

.scroll-area:nth-of-type(4) {
