}

header {
  z-index: 11;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: .5em 1em;
  animation: 1.7s ease-in both slide-in-top;
  display: flex;
  position: fixed;
}

header:before {
  content: "";
  z-index: -1;
  border-top: 15px solid #17bebb;
  width: 100%;
  height: 100%;
  transition: opacity .3s;
  animation: 3s 3s infinite both slide-in-top-after;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}

@keyframes slide-in-top {
  0% {
    transform: translateY(-100px);
  }

  50% {
    transform: translateY(5px);
  }

  80% {
    transform: translateY(10px);
  }

  100% {
    transform: translateY(0);
  }
}

@keyframes slide-in-top-after {
  0% {
    transform: translateY(-15px);
  }

  50% {
    transform: translateY(-13px);
  }

  80% {
    transform: translateY(-14px);
  }

  100% {
    transform: translateY(-15px);
  }
}

.component--slide-out {
  text-align: center;
  width: 100%;
  height: 100vh;
  transition: left 2.5s cubic-bezier(.075, 1.82, .165, 1);
  position: relative;
  top: 0;
  left: 0;
}

